import React, { useEffect, useState } from "react";
import "./App.css";

import {
  Layout,
  Checkbox,
  Radio,
  Menu,
  Breadcrumb,
  Select,
  Row,
  Col,
  Spin,
  Card,
  BackTop,
  Divider,
  Button,
  Space,
  Affix,
} from "antd";
const { Option } = Select;
const { Header, Content, Footer } = Layout;
const CheckboxGroup = Checkbox.Group;

const styleOfBackToTop = {
  height: 40,
  width: 40,
  lineHeight: "40px",
  borderRadius: 4,
  backgroundColor: "#1088e9",
  color: "#fff",
  textAlign: "center",
  fontSize: 14,
};

const App = () => {
  const [loading, setLoading] = useState(true);
  const [txt, setTxt] = useState();
  const [cik, setCIK] = useState("");
  const [filingDate, setFilingDate] = useState("");
  const [usr, setUsr] = useState("adrian");
  const [isSecuredLoan, setIsSecuredLoan] = useState(false);
  const [isReBacked, setIsRebacked] = useState(false);
  const [hasFullRestriction, setHasFullRestriction] = useState(false);
  const [hasNoRestriction, setHasNoRestriction] = useState(false);
  const [hasPartialRestrictions, setHasPartialRestrictions] = useState(false);
  const [partial_expenditure, setPartial_expenditure] = useState(false);
  const [partial_profitability, setPartial_profitability] = useState(false);
  const [partial_no_hostile, setPartial_no_hostile] = useState(false);
  const [partial_proforma, setPartial_proforma] = useState(false);
  const [partial_nodiversifying, setPartial_nodiversifying] = useState(false);
  const [partial_specific, setPartial_specific] = useState(false);
  const [checked_partials, setChecked_partials] = useState([]);
  const [uncheck_secured, setUncheck_secured] = useState(true);
  const [uncheck_re_back, setUncheck_re_back] = useState(true);
  const [uncheck_all, setUncheck_all] = useState(true);

  function getTxt() {
    setLoading(true);

    fetch("https://api.adrian-gao.com/remv/next")
      .then((resp) => resp.json())
      .then((data) => {
        const oldTxt = data["txt"];
        setTxt(oldTxt);
        var filename = data["file"];
        console.log(filename);
        const cikStr = filename.split("_", 1);
        setCIK(cikStr);
        var filename = filename.replace(cikStr + "_", "");
        setFilingDate(filename.split(".", 1));
        setLoading(false);
      });
  }

  useEffect(() => {
    document.title = "REMV Project";
  }, []);
  useEffect(getTxt, []);

  function onChange(value) {
    setUsr(value);
    console.log(`selected ${value}`);
  }

  function onBlur() {}

  function onFocus() {}

  function onSearch(val) {}

  function clickSubmitBtn(e) {
    const date = new Date();

    const data = {
      secured: String(isSecuredLoan),
      isReBacked: String(isReBacked),
      hasFullRestriction: String(hasFullRestriction),
      hasNoRestriction: String(hasNoRestriction),
      hasPartialRestrictions: String(hasPartialRestrictions),
      partial_expenditure: String(partial_expenditure),
      partial_no_hostile: String(partial_no_hostile),
      partial_nodiversifying: String(partial_nodiversifying),
      partial_proforma: String(partial_proforma),
      partial_specific: String(partial_specific),
      partial_profitability: String(partial_profitability),
      user: String(usr),
      cik: String(cik),
      date: String(filingDate),
      submit_time: date.toString(),
    };
    // console.log(data);
    // Re-initiate all states
    setChecked_partials([]);
    setHasPartialRestrictions(false);
    setUncheck_secured(true);
    setUncheck_re_back(true);
    setUncheck_all(true);

    setLoading(true);
    fetch("https://api.adrian-gao.com/remv/submit", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((resp) => resp.json())
      .then((data) => console.log(data))
      .then(getTxt());
  }

  function clickSkipBtn() {
    // Re-initiate all states
    setChecked_partials([]);
    setHasPartialRestrictions(false);
    setUncheck_secured(true);
    setUncheck_re_back(true);
    setUncheck_all(true);

    // Load new
    getTxt();
  }

  return (
    <Layout>
      <Header style={{ position: "fixed", zIndex: 1, width: "100%" }}>
        <div className="logo" />
        <Menu theme="dark" mode="horizontal" defaultSelectedKeys={["2"]}>
          {/* <Menu.Item key="1" disabled={true}>
            Loans Secured by Real Estate
          </Menu.Item> */}
          <Menu.Item key="2" disabled={false}>
            Negative Covenants on Acquisitions
          </Menu.Item>
        </Menu>
      </Header>
      <Content
        className="site-layout"
        style={{ padding: "0 50px", marginTop: 64 }}
      >
        <Breadcrumb style={{ margin: "16px 0" }}>
          <Breadcrumb.Item>{cik}</Breadcrumb.Item>
          <Breadcrumb.Item>8-K</Breadcrumb.Item>
          <Breadcrumb.Item>{filingDate}</Breadcrumb.Item>
        </Breadcrumb>
        <Row gutter={16}>
          <Col span={16}>
            {" "}
            <Spin spinning={loading} size="large">
              <Card
                title="Excerpt of the 8K filing"
                // extra={<a href="#">Full text</a>}
                style={{ width: "100%", minHeight: 600 }}
              >
                <div
                  dangerouslySetInnerHTML={{
                    __html: txt,
                  }}
                ></div>
              </Card>
            </Spin>
          </Col>
          <Col span={8}>
            <Affix offsetTop={80} style={{ float: "right" }}>
              <Row gutter={16}>
                <Select
                  showSearch
                  style={{ width: 200 }}
                  placeholder="Please identify yourself..."
                  optionFilterProp="children"
                  defaultValue={usr}
                  onChange={onChange}
                  onFocus={onFocus}
                  onBlur={onBlur}
                  onSearch={onSearch}
                  filterOption={(input, option) =>
                    option.children
                      .toLowerCase()
                      .indexOf(input.toLowerCase()) >= 0
                  }
                >
                  <Option value="adrian">Adrian</Option>
                  <Option value="alex">Alex</Option>
                  <Option value="buhui">Buhui</Option>
                  <Option value="kai">Kai</Option>
                  <Option value="assistant1">RA 1</Option>
                  <Option value="assistant2">RA 2</Option>
                  {/* <Option value="sherry">Sherry</Option> */}
                  <Option value="other">Other</Option>
                </Select>
              </Row>
              <Spin spinning={loading} size="large">
                <Divider orientation="left">
                  Is this filing about a secured loan?
                </Divider>
                <Space size={"middle"} direction="" align="center" width="100%">
                  <Radio.Group
                    onChange={(e) => {
                      setUncheck_secured(false);
                      setIsSecuredLoan(e.target.value === 1);
                      if (!isSecuredLoan) {
                        setIsRebacked(false);
                      }
                    }}
                    value={uncheck_secured ? null : isSecuredLoan ? 1 : 0}
                  >
                    <Radio value={1}>Yes</Radio>
                    <Radio value={0}>No</Radio>
                  </Radio.Group>
                </Space>
                <Divider orientation="left">
                  If yes, is the loan likely secured by real estate?
                </Divider>
                <Space size={"middle"} direction="" align="center" width="100%">
                  <Radio.Group
                    disabled={!isSecuredLoan}
                    onChange={(e) => {
                      setUncheck_re_back(false);
                      setIsRebacked(e.target.value === 1);
                    }}
                    value={uncheck_re_back ? null : isReBacked ? 1 : 0}
                  >
                    <Radio value={1}>Yes</Radio>
                    <Radio value={0}>No</Radio>
                  </Radio.Group>
                </Space>
                <Divider orientation="left">
                  Please check the negative covenants related to acquisitions.
                </Divider>
                <Space size={"middle"} direction="" align="center" width="100%">
                  <Radio.Group
                    onChange={(e) => {
                      const value = e.target.value;
                      setUncheck_all(false);
                      setHasFullRestriction(value === "full");
                      setHasNoRestriction(value === "no");
                      setHasPartialRestrictions(value === "partial");
                      if (!hasPartialRestrictions) {
                        setPartial_specific(false);
                        setPartial_expenditure(false);
                        setPartial_nodiversifying(false);
                        setPartial_no_hostile(false);
                        setPartial_proforma(false);
                        setPartial_profitability(false);
                      }
                    }}
                    value={
                      uncheck_all
                        ? null
                        : hasFullRestriction
                        ? "full"
                        : hasPartialRestrictions
                        ? "partial"
                        : "no"
                    }
                  >
                    <Radio value={"full"}>Full restriction</Radio>
                    <Radio value={"no"}>No restriction</Radio>
                    <Radio value={"partial"}>Partial restriction</Radio>
                  </Radio.Group>
                </Space>
                <Divider orientation="left">
                  If partial restrictions apply, please select the
                  subcategories.
                </Divider>
                <CheckboxGroup
                  style={{ width: "100%" }}
                  disabled={!hasPartialRestrictions}
                  onChange={(list) => {
                    setChecked_partials(list);
                    setPartial_expenditure(list.includes("A"));
                    setPartial_profitability(list.includes("B"));
                    setPartial_no_hostile(list.includes("C"));
                    setPartial_proforma(list.includes("D"));
                    setPartial_nodiversifying(list.includes("E"));
                    setPartial_specific(list.includes("F"));
                  }}
                  value={checked_partials}
                >
                  <Row>
                    <Col span={24}>
                      <Checkbox value="A">Expenditure limit</Checkbox>
                    </Col>
                    <Col span={24}>
                      <Checkbox value="B">
                        Minimum profitability of target
                      </Checkbox>
                    </Col>
                    <Col span={24}>
                      <Checkbox value="C">No hostile acquisition</Checkbox>
                    </Col>
                    <Col span={24}>
                      <Checkbox value="D">Pro Forma compliance</Checkbox>
                    </Col>
                    <Col span={24}>
                      <Checkbox value="E">Prohibit diversifying</Checkbox>
                    </Col>
                    <Col span={24}>
                      <Checkbox value="F">Specific acquisition target</Checkbox>
                    </Col>
                  </Row>
                </CheckboxGroup>
              </Spin>
              <br></br>
              <Button
                type="primary"
                block
                size="large"
                onClick={clickSubmitBtn}
              >
                Submit
              </Button>
              <br></br>
              <br></br>
              <Button type="secondary" block onClick={clickSkipBtn}>
                Skip
              </Button>
            </Affix>
          </Col>
        </Row>
      </Content>
      <BackTop>
        <div style={styleOfBackToTop}>Top</div>
      </BackTop>
      <Footer style={{ textAlign: "center" }}>
        REMV ©2021 Created by <a href="https://mingze-gao.com">Mingze Gao</a>
      </Footer>
    </Layout>
  );
};
export default App;
